
import { Options, Vue } from 'vue-class-component'
import RichTextEditor from '@/components/RichTextEditor.vue'

@Options({
  props: {
    userAvatarUrl: String,
    userName: String,
    content: String,
    level: Number,
    date: String,
    isPoster: Boolean,
    isYou: Boolean,
    isLoading: Boolean,
    hasLike: {
      type: Number,
      default: 0 // 1 = like, 2 = dislike
    },
    likeNum: {
      type: Number,
      default: 0
    },
    dislikeNum: {
      type: Number,
      default: 0
    },
    hasEdited: Boolean,
    canPin: Boolean
  },
  components: {
    'rich-text-editor': RichTextEditor
  },
  emits: ['replyTextClick', 'deleteTextClick', 'saveTextClick', 'likeClick', 'dislikeClick'],
  data () {
    return {
      displayEditor: false,
      editContent: '666'
    }
  },
  computed: {
    canDelete () {
      return this.isYou
    }
  },
  mounted () {
    this.showContent(this.content)
  },
  watch: {
    content (newValue) {
      this.showContent(newValue)
    },
    displayEditor (newValue) {
      if (newValue) {
        this.editContent = this.content
      } else {
        setTimeout(() => {
          this.showContent(this.content)
        }, 0)
      }
    }
  },
  methods: {
    showContent (html:string) {
      this.$refs.content.innerHTML = html
    }
  }
})
export default class PostPageLevel extends Vue {}
